<template>
<div id="back">
  <div class="backwrapper">
    <reback></reback>
  </div>
</div>
</template>

<script type="text/ecmascript-6">
import reback from '../reback/reback.vue'
export default {
  components: {
    'reback': reback
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
#back
  position relative
  top 130px
  background-color #f3f4f6
  height 100%
  overflow-y auto
.backwrapper
  max-width 700px
  height 100%
  margin 0 auto
  padding-top 22px
</style>



